    mainlogoTl.to("#c1", 1.5, { morphSVG: "#d1", css: { visibility: "visible" } }, 0.5);
    mainlogoTl.to("#c2", 1.5, { morphSVG: "#d8", css: { visibility: "visible" } }, 0.5);
    mainlogoTl.to("#c3", 1.5, { morphSVG: "#d6", css: { visibility: "visible" } }, 0.5);
